.todo_header{
    width: 100%;
    height: 30px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.todo_header__title{
    color: rgb(1, 214, 253);
}
.todo_header__nav{
    display: flex;
    justify-content: space-around;
    width: 250px;
    cursor: pointer;
}
.todo_header__nav_item.active{
    color: gold;
}